body {
  font-size: x-large;
  background-color: #19083b;
  color: white;
  margin: 0px 0px;
  font-family: cursive;
}

.box {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  word-wrap: break-word;
  align-items: center;
  margin: 2% 25%;
}

.data-box {
  display: flex;
  flex-direction: column;
  align-content: center;
  flex-wrap: wrap;
  animation: abi 1.5s ease-in-out 0s 1 forwards;
}

table,
tr,
th,
td {
  font-size: large;
  text-align: center;
  padding: 10px 35px;
  border: 2px solid rgb(0, 221, 255);
  border-collapse: collapse;
  box-sizing: border-box;
  margin: 50px 20px;
}

th {
  font-weight: bolder;
  font-size: larger;
  font-style: italic;
}

#feed {
  /* width: 90%; */
  /* height: 50px;  */
  word-wrap: break-word;
}


.hover_button:hover{
  background-color: rgb(0, 235, 252);
  /* width: 500px;
  border-radius: 50%; */
} 

.container1 {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  top: 0px;
  align-items: center;
  background-color: #150734;
}

span {
  color: rgb(44, 247, 33);
}

.personal { 
  animation: magnify 1.5s ease-in-out 0s backwards;
  width: 300px; 
}

@keyframes magnify {
  from {
    width: 10px;
  }

  to {
    width: 300px;
  }
}

@keyframes upflow {
  from {
    margin-top: 100%;
  }

  to {
    margin-top: 0%;
    /* transform: rotate(360deg); */
  }
}

.li {
  color: grey;
}

a {
  text-decoration: none;
  color: white;
}

.flex-1 {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
}

.flex-2 {
  display: flex;
  margin-top: 25px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
}

#feed-box {
  animation: upflow 1.5s ease-in-out 0s 1 forwards;
}

button {
  background-color: #2c0e6d;
  color: white;
  cursor: pointer;
  font-size: large;
  font-family: cursive;
  width: 200px;
  height: 35px;
  border-radius: 10px;
  border: 2px solid rgb(5, 37, 180);
  margin: 25px 0px;
  /* margin-top: 30px; */
  /* margin-bottom: 50px; */
}
 

.project {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  flex-wrap: wrap;
  margin: 1% 35%;
  animation: upflow 1.5s ease-in-out 0s backwards;
}

.about {
  animation: upflow 2s ease-in-out 0s 1 forwards;
}

#image {
  width: 60%;
  animation: abi 1.5s ease-in-out 0s 1 forwards;
}

@keyframes abi {
  from {
    margin-left: 100%;
  }

  to {
    margin-left: 0%;
  }
}

.pro {
  margin-top: 30px;
  width: 100%;
  height: 100%;
}

footer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  padding-top: 10px;
}

.all {
  margin: 40px 20px;
  width: 25%;
  border-radius: 50%;
}

li {
  margin-top: 30px;
}

li span {
  color: red;
  animation: ski 1.5s ease-in-out 0s 1 forwards;

}

@keyframes ski {
  from {
    color: white; 
  }

  to { 
    color: rgb(255, 0, 111); 
  }
}





@media screen and (max-width: 1023px) {
  .all {
    width: 30%;
  }

  body {
    font-size: larger;
  }

  .image {
    width: 90%;
  }

  #image {
    width: 100%;
  }


  .box{
    margin:2% 5% ;
  }

  .personal {
    width: 50%;
    /* height: 50%; */
    padding-top: 50px;
    animation: rotate 1.5s ease-in-out 0s 1 forwards;
  }

  @keyframes rotate {
    from {
      width : 1% ; 
    }

    to {
      width: 50% ; 
    }
  }

  .container1 {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }

  .flex-1 {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }

  .flex-2 {
    padding: 0px 35px;
  }

  button {
    margin: 20px 0px;
  }

  .project {
    margin: 1% 20%;
  }
 
  table,
  tr,
  th,
  td {
    font-size: 90%;
    padding: 4% 0%;
  }
}
